:root {
  --black: hsl(0, 0%, 0%);
  /* Text */
  --pale-violet: hsl(276, 100%, 81%);
  --mod-violet: hsl(276, 55%, 52%);
  --dk-violet: hsl(271, 15%, 43%);
  --grayish-blue: hsl(206, 6%, 79%);
  --vry-dk-violet: hsl(271, 36%, 24%);
  --dk-gr-violet: hsl(270, 7%, 64%);
  /* Gradients */
  --lt-magenta: hsl(293, 100%, 63%);
  --lt-violet: hsl(264, 100%, 61%);
  /* Secondary */
  --white: hsl(0, 0%, 100%);
  --lt-gr-violet: hsl(270, 20%, 96%);
  --vry-lt-magenta: hsl(289, 100%, 72%);
}